<template>
  <div>
    <bread-nav :nav="nav"></bread-nav>
    <main-part>
      <div class="btn-block">
        <div class="btn-main mr15"
             @click="ClickDateLine">导出
        </div>
        <div class="btn-main" @click="dialog=true">导入</div>
      </div>
      <search-box :searchData="searchData" :table="table"></search-box>
      <resize-table>
      <table class="table">
        <thead>
        <tr>
          <th width="20%">姓名</th>
          <th width="20%">考核时间</th>
          <th width="20%">考核扣分原因</th>
          <th width="20%">绩效等级分数（分）</th>
          <th width="20%">绩效金额（元）</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="(item,key) in table.list">
          <td>{{item.teacherName}}</td>
          <td>{{item.assessDate | substrTime }}</td>
          <td>{{item.deductionReason }}</td>
          <td>{{item.performanceScore}}</td>
          <td>{{item.performanceBonus}}</td>
        </tr></tbody>
      </table>
      </resize-table>
      <no-data v-show="!table.noData"></no-data>
      <paging :data.sync="table"></paging>
      <!--导入-->
      <el-dialog title="导入" class="dialog" :visible.sync="dialog" width="540px">
        <div class="item">
          <span class="title imp">月份</span>
          <el-date-picker
                  type="month"
                  v-model="month"
                  placeholder="选择月份" style="width: 86%">
          </el-date-picker>
        </div>
        <import-excel @change="change" type="学工各线考核导入模板"/>
        <div class="dialog_btns">
          <div @click="impFun" class="btn-submit btn-upload mr15">确认</div>
          <div @click="dialog = false" class="btn-clear">返回</div>
        </div>
      </el-dialog>
    </main-part>
  </div>
</template>
<script>
  import mixins from '../../../../assets/js/mixins'

  export default {
    mixins: [mixins.$exportDetail],
    data () {
      return {
        nav: [{name: '各线考核', path: ''}],
        searchData: [
          {
            type: 'date', title: '月份', dateType: 'month',
            model: 'month', initialDate: new Date(new Date().setMonth(new Date().getMonth() - 1))
          },
        ],
        table: {
          url: '/assessStaff/queryAssessStaffPage.do',
          el: '.table',
          total: 0,
          noData: true,
          list: [],
          query: {
            month: new Date(new Date().setMonth(new Date().getMonth() - 1)).$Format('yyyy-MM-dd hh:mm:ss'),
          }
        },
        dialog: false,
        month: null,
        // 所有的周
        weekList: [],
        form: {
          weekId: null,
          courseId: null,
        }
      }
    },
    methods: {
      // 处理时间
      ClickDateLine(){
        let newDate = this.table.query.month
        this.table.query.month=this.table.query.month.substr(0,10)
        this.table.query.month=this.table.query.month.replace(/(\d{4})-(\d{2})-(\d{2})/g,'$1/$2/$3')
        this.$exportDetail('/export/assessStaffExport.do')
        this.table.query.month = newDate
      },
      // 获取周
      async getWeek () {
        this.weekList = await this.$ajax({
          url: '/school/querySchoolTermWeekListWithCurrentTerm.do'
        })
      },
      change (e) {
        this.files = e.target.files[0]
      },
      // 导入
      async impFun () {
        if (!this.month) {
          this.$message.warning('请选择月份')
          return
        }
        let file = this.files
        if (!file) {
          this.$message.warning('请选择上传文件')
          return
        }
        let formData = new FormData()
        formData.append('file', file)
        formData.append('month', this.month)
        let obj = {
          url: '/import/importAssessStaff.do',
          query: formData,
          el: '.btn-upload',
          noText: true
        }
        // 上传模板拿到返回的模板地址
        let res = await this.$ajax(obj)
        if (res.errMsgList && res.errMsgList.length) {
          this.$message.error(res.errMsgList[0])
        } else {
          // this.$refs.upload.value = null
          this.$remind('导入成功')
          this.dialog = false
          this.$search(this.table)
        }
      },
    },
    async mounted () {
      await this.$search(this.table)
    }
  }
</script>
<style lang="sass" scoped>
  .dialog
    .title
      width: 70px
      line-height: 40px
      vertical-align: top
      display: inline-block
    .input
      width: calc(100% - 110px)
    .imp:before
      content: '*'
      color: $error-c
      margin-right: 4px
    .file-input
      /deep/ input
        padding-top: 10px
        line-height: 1
    .item
      margin-bottom: 20px
    .dialog_btns
      margin-top: 15px
      text-align: center
</style>
